<template>
  <div id="app">
    <textarea v-model="content" placeholder="Convert Janpanese to Romaji" type="text"></textarea>
    <button @click="converte"
            class="btn-convert"
            :class="{ 'no-content': !content, 'cleanup': result }">
      <template v-if="result">返回</template>
      <template v-else>开始转换</template>
    </button>
    <div v-if="loading" class="loader"></div>
    <div v-if="errored" class="error">😯转换出错，请检查是否含有非和制汉字</div>
    <div v-if="result" class="result">
      <template v-for="({text, furigana}, index) in result">
        <template v-if="text.includes('|')">
          <br :key="index">
          <ruby v-if="text.length > 1" class="word" :key="index">
            {{text.replace(/\|/g, '')}}
            <rt></rt>
          </ruby>
        </template>
        <ruby v-else class="word" :key="index">
          {{text}}
          <rt>{{furigana === '' ? '&nbsp;' : furigana}}</rt>
        </ruby>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      loading: false,
      errored: false,
      content: '',
      result: null,
    }
  },
  methods: {
    converte () {
      if (this.result) {
        this.result = null
      } else {
        this.errored = false
        this.loading = true
        const content = this.content.replace(/\n/g, "|")
        const data = {
          content,
        };
        const body = Object.entries(data).map(([key, value]) => {
          return `${key}=${encodeURIComponent(value)}`;
        }).join('&');

        fetch('https://ibdkopi6vn.erjsnfs.com/api/v1/furigana/translate', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
          },
          body,
          mode: 'cors',
        })
          .then(async (response) => {
            const result = await response.json();
            if (result.success) {
              this.result = result.data.list;
            } else {
              this.handleError();
            }
          })
          .catch(this.handleError)
          .finally(() => this.loading = false)
      }
    },
    handleError () {
      this.errored = true
      setTimeout(() => {
        this.errored = false
      }, 2000)
    },
  },
}
</script>

